<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20">
        <el-col :span="6">
          <el-input placeholder="查找商品" v-model="queryInfo.query">
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getGoodsList"
            ></el-button>
          </el-input>
        </el-col>
        <!-- <el-col :span="4">
          <el-button type="primary">添加商品</el-button>
        </el-col> -->
      </el-row>
      <el-table :data="goodsList">
        <el-table-column type="index" label="#"></el-table-column>
        <!-- <el-table-column prop="goods_id" label="商品id"></el-table-column> -->
        <el-table-column prop="goods_name" label="商品名称"></el-table-column>
        <el-table-column prop="goods_price" label="单价" width="100px">
        </el-table-column>
        <el-table-column label="更新时间" width="200px">
          <template slot-scope="scope">
            <span>{{ scope.row.upd_time | dateFormat }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="openEditDialog(scope.row.goods_id)"
              >修改</el-button
            >
            <el-button
              type="danger"
              icon="el-icon-delete"
              @click="openDeleteDialog(scope.row.goods_id)"
              size="mini"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="queryInfo.pagenum"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <el-dialog title="修改商品信息" :visible.sync="editDialogVisible">
      <el-form
        :model="goodsInfo"
        :rules="editRule"
        label-width="80px"
        ref="editForm"
      >
        <el-form-item label="商品名" prop="goods_name">
          <el-input
            prefix-icon="el-icon-edit-outline"
            v-model="goodsInfo.goods_name"
          ></el-input>
        </el-form-item>
        <el-form-item label="单价" prop="goods_price">
          <el-input
            prefix-icon="el-icon-coin"
            v-model="goodsInfo.goods_price"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editGoods">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10,
      },
      goodsList: [],
      total: 0,
      goodsInfo: {},
      editRule: {
        goods_name: [
          { required: true, message: "请输入商品名", trigger: "blur" },
        ],
        goods_price: [
          { required: true, message: "请输入价格", trigger: "blur" },
        ],
      },
      editDialogVisible: false,
    };
  },
  methods: {
    async getGoodsList() {
      const { data: res } = await this.$http.get("goods", {
        params: this.queryInfo,
      });
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("获取失败");
      } else {
        this.goodsList = res.data.goods;
        this.total = res.data.total;
      }
    },
    async queryGoods(id) {
      const { data: res } = await this.$http.get(`goods/${id}`);
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("查询失败");
      } else {
        this.goodsInfo = res.data;
      }
    },
    editGoods() {
      this.$refs.editForm.validate(async (valid) => {
        if (!valid) {
          return;
        } else {
          const { data: res } = await this.$http.put(
            `goods/${this.goodsInfo.goods_id}`,
            this.goodsInfo
          );
          // console.log(res);
          if (res.meta.status !== 200) {
            this.$message.error("修改失败");
          } else {
            this.$message.success("修改成功");
            this.editDialogVisible = false;
            this.getGoodsList();
          }
        }
      });
    },
    async removeGoods(id) {
      const { data: res } = await this.$http.delete(`goods/${id}`);
      // console.log(res);
      if (res.meta.status !== 200) {
        this.$message.error("删除失败");
      } else {
        this.$message.success("删除成功");
        this.getGoodsList();
      }
    },
    openEditDialog(id) {
      this.queryGoods(id);
      this.editDialogVisible = true;
    },
    openDeleteDialog(id) {
      // console.log(id);
      this.$confirm("此操作将永久删除该商品, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.removeGoods(id);
        })
        .catch(() => {
          this.$message.info("已取消删除");
        });
    },
    handleSizeChange(val) {
      this.queryInfo.pagesize = val;
      this.queryInfo.pagenum = 1;
      this.getGoodsList();
    },
    handleCurrentChange() {
      this.getGoodsList();
    },
    search() {
      this.queryInfo.pagenum = 1;
      this.getGoodsList();
    },
  },
  created() {
    this.getGoodsList();
  },
};
</script>

